<template>
  <v-dialog
    v-model="showDialog"
    fullscreen
    hide-overlay
    transition="dialog-bottom-transition"
    scrollable
  >
    <v-card tile>
      <v-toolbar card dark color="primary">
        <v-btn icon dark @click.native="onClose()">
          <v-icon>close</v-icon>
        </v-btn>
        <v-toolbar-title>Education Checklist</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn dark flat @click.native="onSave()">Save</v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-card-text>
        <v-container>
          <v-layout row wrap>
            <v-card width="100%" class="sub-card">
              <v-container fluid grid-list-md>
                <v-layout row wrap>
                  <v-flex xs12 md6 pr-1>
                    <v-text-field v-model="education.physician_name" label="Physician Name" disabled></v-text-field>
                  </v-flex>
                  <v-flex xs12 md6> 
                    <v-text-field v-model="education.auditor_name" label="Auditor" disabled></v-text-field>
                  </v-flex>          
                </v-layout>
                <v-layout row wrap>
                  <v-flex xs12 md4 pr-1>
                    <v-select v-model="education.type" :items="educationTypes" label="Type of Education"></v-select>
                  </v-flex>
                  <v-flex xs12 md2 pr-1> 
                    <v-select v-model="education.method" :items="educationMethods" label="Method"></v-select>
                  </v-flex>          
                  <v-flex xs12 md3 pr-1>
                    <v-menu
                      ref="menuDateToday"
                      :close-on-content-click="false"
                      v-model="menuDateToday"
                      :nudge-right="40"
                      :return-value.sync="education.education_date"
                      lazy
                      transition="scale-transition"
                      offset-y
                      full-width
                      min-width="290px"
                    >
                      <v-text-field
                        slot="activator"
                        v-model="education.education_date"
                        label="Today's Date"
                        prepend-icon="event"
                        readonly
                      ></v-text-field>
                      <v-date-picker v-model="education.education_date" @input="$refs.menuDateToday.save(education.education_date)"></v-date-picker>
                    </v-menu>
                  </v-flex>
                  <v-flex xs12 md3>
                    <v-menu
                      ref="menuDateStart"
                      :close-on-content-click="false"
                      v-model="menuDateStart"
                      :nudge-right="40"
                      :return-value.sync="education.start_date"
                      lazy
                      transition="scale-transition"
                      offset-y
                      full-width
                      min-width="290px"
                    >
                      <v-text-field
                        slot="activator"
                        v-model="education.start_date"
                        label="Start Date (if applicable)"
                        prepend-icon="event"
                        readonly
                      ></v-text-field>
                      <v-date-picker v-model="education.start_date" @input="$refs.menuDateStart.save(education.start_date)"></v-date-picker>
                    </v-menu>
                  </v-flex>                    
                </v-layout>
              </v-container>
            </v-card>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-card width="100%" class="sub-card">
              <v-card-title class="sub-header">
                <v-checkbox label="Evaluation and Management (E/M) Services" v-model="education.emservices" ma-0 pa-0></v-checkbox>
              </v-card-title>
              <v-container fluid>
                <v-layout row wrap>
                  <v-flex xs12 md4>
                    <v-checkbox label="Chief Complaint" v-model="education.emservices_chief_complaint" @change="setEMServices()"  ma-0 pa-0></v-checkbox>
                    <v-checkbox label="HPI" v-model="education.emservices_hpi"></v-checkbox>
                    <v-checkbox label="ROS" v-model="education.emservices_ros"></v-checkbox>
                    <v-checkbox label="PFSH" v-model="education.emservices_pfsh"></v-checkbox>
                  </v-flex>
                  <v-flex xs12 md4>
                    <v-layout row>
                      <v-flex xs12 md4>
                        <v-checkbox label="PE" v-model="education.emservices_pe"></v-checkbox>
                      </v-flex>
                      <v-flex xs12 md4>
                        <v-checkbox label="1995" v-model="education.emservices_1995"></v-checkbox>
                      </v-flex>
                      <v-flex xs12 md4>
                        <v-checkbox label="1997" v-model="education.emservices_1997"></v-checkbox>
                      </v-flex>
                    </v-layout>
                    <v-checkbox label="MDM" v-model="education.emservices_mdm"></v-checkbox>
                    <v-checkbox label="Counseling and coordination of care > 50% of encounter" v-model="education.emservices_counseling"></v-checkbox>
                  </v-flex>
                  <v-flex xs12 md4>
                    <v-checkbox label="PowerPoint presentations provided as f/u via email" v-model="education.aaemservices_pptx_providedaa"></v-checkbox>
                    <v-checkbox label="" v-model="education.emservices_other1"></v-checkbox>
                    <v-checkbox label="" v-model="education.emservices_other2"></v-checkbox>
                    <v-checkbox label="" v-model="education.emservices_other3"></v-checkbox>
                    <v-checkbox label="E/M Coding cards provided" v-model="education.emservices_cards_provided"></v-checkbox>
                  </v-flex>
                </v-layout>
                <v-layout>
                  <v-flex xs12 md6>
                    <v-checkbox label="Advanced Care Planning" v-model="education.emservices_advanced_care_planning"></v-checkbox>
                    <v-checkbox label="Chronic Care Management" v-model="education.emservices_chronic_care_management"></v-checkbox>
                    <v-checkbox label="Medicare Annual Wellness Visit" v-model="education.emservices_medicare_wellness_visit"></v-checkbox>
                    <v-checkbox label="New vs Established Patient Visists" v-model="education.emservices_new_established_visits"></v-checkbox>
                    <v-checkbox label="Observation Care" v-model="education.emservices_observation_care"></v-checkbox>
                    <v-checkbox label="Other Services:" v-model="education.emservices_other_services"></v-checkbox>
                  </v-flex>
                  <v-flex xs12 md6>
                    <v-checkbox label="Critical Care - Time Based" v-model="education.emservices_critical_care"></v-checkbox>
                    <v-checkbox label="Home Visits" v-model="education.emservices_home_visits"></v-checkbox>
                    <v-checkbox label="Medicare Initial Preventive Physician Examination" v-model="education.emservices_medicare_initial_exam"></v-checkbox>
                    <v-checkbox label="Nursing Facility" v-model="education.emservices_nursing_facility"></v-checkbox>
                    <v-checkbox label="Transitional Care Management" v-model="education.emservices_transitional_management"></v-checkbox>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12 md6 pr-1>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox label="CMS Incident-To Criteria" v-model="education.incidentto"></v-checkbox>
                </v-card-title>
                <v-container fluid>
                  <v-layout row wrap>
                    <v-flex xs12 md4>
                      <v-checkbox label="Direct Supervision" v-model="education.incidentto_direct_supervision"></v-checkbox>
                      <v-checkbox label="Employed/Leased" v-model="education.incidentto_employed"></v-checkbox>
                      <v-checkbox label="No new patients/problems" v-model="education.incidentto_patients_problems"></v-checkbox>
                    </v-flex>
                  </v-layout>            
                </v-container>
              </v-card>
            </v-flex>
            <v-flex xs12 md6>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox label="CMS Split/Shared Services" v-model="education.sharedservices"></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12 md6 pr-1>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox label="Non-physician (MLP) Direct Billing" v-model="education.mlp_direct_billing"></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox label="CMS Policies" v-model="education.policies"></v-checkbox>
                </v-card-title>
                <v-container fluid>
                  <v-layout row wrap>
                    <v-flex xs12 md6>
                      <v-checkbox label="Addendums" v-model="education.policies_addendums"></v-checkbox>
                      <v-checkbox label="CHS Provider Coding and Documentation Review Policy" v-model="education.policies_review_policy"></v-checkbox>
                      <v-checkbox label="Other Policy:" v-model="education.policies_other"></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md6>
                      <v-checkbox label="Copy/Paste" v-model="education.policies_copy_paste"></v-checkbox>
                      <v-checkbox label="Scribe Services in Ambulatory Practices" v-model="education.policies_scribe_services"></v-checkbox>
                    </v-flex>
                  </v-layout>            
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12 md6 pr-1>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox label="ICD-10-CM Coding Guidelines" v-model="education.coding_guidelines"></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
            <v-flex xs12 md6>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox label="CMS Global Surgical Rules" v-model="education.surgical_rules"></v-checkbox>
                </v-card-title>
                <v-container fluid>
                  <v-layout row wrap>
                    <v-flex xs12 md6>
                      <v-checkbox label="Fact sheet sent via email" v-model="education.surgical_rules_fact_sheet"></v-checkbox>
                    </v-flex>
                  </v-layout>            
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox label="Modifiers" v-model="education.modifiers"></v-checkbox>
                </v-card-title>
                <v-container fluid>
                  <v-layout row wrap>
                    <v-flex xs12 md2>
                      <v-checkbox label="24" v-model="education.modifiers_24"></v-checkbox>
                      <v-checkbox label="80, 81, 82" v-model="education.modifiers_80_81_82"></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md2>
                      <v-checkbox label="25" v-model="education.modifiers_25"></v-checkbox>
                      <v-checkbox label="AS" v-model="education.modifiers_as"></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md2>
                      <v-checkbox label="50, RT, LT" v-model="education.modifiers_50_rt_lt"></v-checkbox>
                      <v-checkbox label="A1" v-model="education.modifiers_a1"></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md2>
                      <v-checkbox label="57" v-model="education.modifiers_57"></v-checkbox>
                      <v-checkbox label="Other Modifier(s):" v-model="education.modifiers_other"></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md2>
                      <v-checkbox label="59" v-model="education.modifiers_59"></v-checkbox>
                    </v-flex>
                    <v-flex xs12 md2>
                      <v-checkbox label="62" v-model="education.modifiers_62"></v-checkbox>
                    </v-flex>
                  </v-layout>            
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox label="Teaching Physician Rules" v-model="education.teaching_rules"></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12 md6 pr-1>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox label="Charge Ticket Review" v-model="education.ticket_review"></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
            <v-flex xs12 md6>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-checkbox label="Consultations: Request from another provider/Reason Letter to requesting provider" v-model="education.consulations"></v-checkbox>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>

          <v-layout row wrap pt-2>
            <v-flex xs12>
              <v-card width="100%" class="sub-card">
                <v-card-title class="sub-header">
                  <v-container>
                    <v-layout row>
                      <v-flex xs12>
                        <v-checkbox label="Questions:" v-model="education.questions"></v-checkbox>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex xs12>
                        <v-textarea v-model="education.questions_text" rows="2"></v-textarea>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card-title> 
              </v-card>
            </v-flex>
          </v-layout>

        </v-container>
      </v-card-text>
    </v-card>

  </v-dialog>
</template>

<script>
export default {
  name: "dialogEducation",
  data: function() {
    return {
      menuDateToday: false,
      menuDateStart: false
    };
  },
  props: {
    showDialog: Boolean,
    education: { type: Object, default: () => {} }
  },
  computed: {
    educationTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(row => row.keyword === "EDUCATION_TYPE").map(row => row.value);
    },
    educationMethods() {
      return this.$store.getters.APPLICATION_LISTS.filter(row => row.keyword === "EDUCATION_METHOD").map(row => row.value);
    },
    isEMServicesSelected() {
      return (
        this.education.emservices_chief_complaint ||
        this.education.emservices_hpi ||
        this.education.emservices_ros ||
        this.education.emservices_pfsh ||
        this.education.emservices_pe ||
        this.education.emservices_1995 ||
        this.education.emservices_1997 ||
        this.education.emservices_mdm ||
        this.education.emservices_counseling ||
        this.education.emservices_pptx_provided ||
        this.education.emservices_cards_provided ||
        this.education.emservices_other1 ||
        this.education.emservices_other2 ||
        this.education.emservices_other3 ||
        this.education.emservices_other1_text ||
        this.education.emservices_other2_text ||
        this.education.emservices_other3_text ||
        this.education.emservices_advanced_care_planning ||
        this.education.emservices_chronic_care_management ||
        this.education.emservices_medicare_wellness_visit ||
        this.education.emservices_new_established_visits ||
        this.education.emservices_observation_care ||
        this.education.emservices_critical_care ||
        this.education.emservices_home_visits ||
        this.education.emservices_medicare_initial_exam ||
        this.education.emservices_nursing_facility ||
        this.education.emservices_transitional_management ||
        this.education.emservices_other_services
      );
    }
  },
  methods: {
    onClose: function() {
      this.$emit("interfaceClose", false);
    },
    onSave: function() {
      this.$emit("interfaceSave", false);
    },
    setEMServices: function() {
      this.education.emservices = this.isEMServicesSelected;
    }
  }
};
</script>

<style>
.sub-header {
  font-weight: bold;
}

.sub-card {
  background-color: #e0ffff !important;
}
</style>