<template>
  <v-snackbar
    v-model="snackbar"
    :timeout="timeout"
    :multi-line="multiline === true"
    :top="top"
    :color="color">
      {{ text }}
      <v-btn flat dark @click="snackbar = false">Close</v-btn>
  </v-snackbar>
</template>

<script>
export default {
  name: "CustomSnackbar",
  data() {
    return {
      snackbar: false,
      text: null,
      color: null,
      timeout: null,
      multiline: null,
      top: true
    };
  },
  methods: {
    showSnackbar: function(text, color, timeout = 0, multiline = false, top = true) {
      this.text = text;
      this.color = color;
      this.timeout = timeout;
      this.top = top;
      this.multiline = multiline;
      this.snackbar = true;
    }
  }
};
</script>
