<template>
<div>
  <v-container>
    <custom-snackbar ref="customSnackbar"></custom-snackbar>
    <v-card class="mb-2">
      <v-card-title class="headline font-weight-regular blue-grey white--text">Start An Audit</v-card-title>
      <v-card-text>
        <v-subheader class="pa-0">Physician</v-subheader>
        <v-layout row wrap>
          <v-flex xs12 md6>
            <v-autocomplete
              v-model="selectedNpi"
              :items=physicianLookup
              persistent-hint
              item-text="name"
              item-value="npi"
            >
            </v-autocomplete>
          </v-flex>
          <v-flex xs12 md3>
            <v-menu
              :close-on-content-click="false"
              v-model="menu1"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              max-width="290px"
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="auditBeginDate"
                label="Begin Date"
                persistent-hint
                prepend-icon="event"
                readonly
              ></v-text-field>
              <v-date-picker v-model="auditBeginDate" no-title @input="menu1 = false"></v-date-picker>
            </v-menu>
        </v-flex>
          <v-flex xs12 md3>
            <v-menu
              :close-on-content-click="false"
              v-model="menu2"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              max-width="290px"
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="auditEndDate"
                label="End Date"
                persistent-hint
                prepend-icon="event"
                readonly
              ></v-text-field>
              <v-date-picker v-model="auditEndDate" no-title @input="menu2 = false"></v-date-picker>
            </v-menu>            
          </v-flex>
        </v-layout>
        <v-layout row wrap>
          <v-flex xs12 md6>
            <v-autocomplete
              v-model="auditPayer"
              :items=payerListing
              persistent-hint
              item-text="original_payor"
            >
            </v-autocomplete>            
          </v-flex>
        </v-layout>
        <v-layout row wrap>
          <v-flex xs12>
            <v-btn block color="blue" @click="searchProcedures()">Search</v-btn>
          </v-flex>
        </v-layout>
      </v-card-text>
    </v-card>

    <v-layout row wrap>
      <v-flex xs12 md8 class="pr-1">
        <v-card class="mb-2">
          <v-card-title class="headline font-weight-regular blue-grey white--text">Procedure Summary</v-card-title>
          <v-card-text>
            <v-list>
              <v-list-tile v-if="this.distinctProcedures.length === 0 && initialSearch">No Data Found</v-list-tile>
              <v-list-group v-for="procedure in this.distinctProcedures" :key="procedure" no-action>
                <v-list-tile slot="activator">
                  <v-list-tile-content>
                    <v-list-tile-title>{{ procedure }} -- {{ listGroupCountForProcedures(procedure) }}</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
                <v-list-tile class="record-selector" v-for="(item, index) in procedureListing" :key="index" v-if="item.cpt === procedure" @click="addPatientToList(item.encounter)">
                  <v-list-tile-content>
                    <v-list-tile-title>{{ item.patient_name }}</v-list-tile-title>
                    <v-list-tile-sub-title>
                      <v-layout row wrap>
                        <v-flex xs12 lg6>
                          CPT: {{ patientCptList(item.encounter).toString().replace(/[\"]/, "") }}
                        </v-flex>  
                        <v-flex xs12 lg6>
                          Service Date: {{ item.service_date }}
                        </v-flex>  
                      </v-layout>
                    </v-list-tile-sub-title>
                    <v-list-tile-sub-title>
                      <v-layout row wrap>
                        <v-flex xs12 lg6>
                          Location: {{ item.location }}
                        </v-flex>  
                        <v-flex xs12 lg6>
                          Diagnosis Codes: {{ item.icd10_billed }}
                        </v-flex>  
                      </v-layout>                      
                    </v-list-tile-sub-title>
                  </v-list-tile-content>
                  <v-list-tile-action>
                    <v-icon color="grey darken-1">arrow_forward</v-icon>
                  </v-list-tile-action>
                </v-list-tile>             
              </v-list-group>
            </v-list>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs21 md4>
        <v-card class="mb-2">
          <v-card-title class="headline font-weight-regular blue-grey white--text">Selected</v-card-title>
          <v-card-text>
            <v-list>
                <v-list-tile v-if="selectedEncounters.length === 0 && initialSearch">No Records Selected</v-list-tile>
                <v-list-tile v-for="(sel, index) in selectedEncounters" :key="index" @click="popPatientFromList(sel)">
                  <v-list-tile-action>
                    <v-icon>arrow_back</v-icon>
                  </v-list-tile-action>
                  <v-list-tile-content >
                    <v-list-tile-title >{{ patientFromEncounter(sel).patient_name }}</v-list-tile-title>
                    <v-list-tile-sub-title>{{ sel }}</v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>             
            </v-list>
          </v-card-text>
          <div class="text-xs-center">
            <v-btn v-if="selectedEncounters.length" @click="createAudits()">Create Audits</v-btn>
          </div>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</div>
</template>

<script>
import CustomSnackbar from "@/components/Snackbar";
import Axios from "axios";
import Config from "../config";

export default {
  name: "Main",
  components: { CustomSnackbar },
  data: () => ({
    auditBeginDate: "2017-12-01",
    auditEndDate: "2018-11-15",
    auditPayer: "MEDICARE",
    selectedNpi: "1245218619",
    menu1: false,
    menu2: false,
    procedureListing: [],
    selectedEncounters: [],
    payerListing: [],
    initialSearch: false
  }),
  async mounted() {
    //   this.currentPhysician = this.changer("");
    const result = await Axios.get(Config.apiRoot + "/transactions/payers");
    this.payerListing = result.data;
  },
  computed: {
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    distinctProcedures() {
      return [...new Set(this.procedureListing.map(obj => obj.cpt))];
    }
  },
  methods: {
    async searchProcedures() {
      this.procedureListing = [];
      this.selectedEncounters = [];
      // not needed elsewhere.  just doing it here.
      const qs = `${this.selectedNpi}/${this.auditBeginDate}/${this.auditEndDate}/${this.auditPayer}`;
      const results = await Axios.get(Config.apiRoot + "/transactions/" + qs);
      if (results.data) {
        this.procedureListing = results.data;
      }
      this.initialSearch = true; // show no data only after the first search is done
    },
    patientCptList(pt) {
      // pt - patient encounter
      // return array of CPT codes for a specific patient
      return [...new Set(this.patientRecords(pt).map(obj => obj.cpt))];
    },
    patientRecords(pt) {
      // pt - patient encounter
      // return array of rows for a specific patient
      return this.procedureListing.filter(obj => obj.encounter === pt);
    },
    listGroupCountForProcedures(proc) {
      return this.procedureListing.filter(obj => obj.cpt === proc).length;
    },
    addPatientToList(enc) {
      // Add encounter to list to create audits
      if (!this.selectedEncounters.includes(enc)) {
        this.selectedEncounters.push(enc);
      }
    },
    popPatientFromList(enc) {
      // Remove encounter from the list of audits
      this.selectedEncounters.pop(enc);
    },
    async createAudits() {
      // create audit records in database
      console.log("creating audit records");
      const qs = `?enc=${this.selectedEncounters}`;

      const res = await Axios.post(Config.apiRoot + "/transactions/createAudits", { encounters: this.selectedEncounters });
      if (!res.data.status) {
        this.showMessage("error", "there was a problem creating the audit records");
      }
      //      this.showMessage("success", res.data.results);
      this.showMessage("success", "Audit records created.  TODO - create detailed message");
      this.selectedEncounters = [];
    },
    patientFromEncounter(enc) {
      return this.procedureListing.find(elem => elem.encounter === enc);
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 5000, true);
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}

.record-selector .v-list__tile {
  height: auto;
}
</style>
